import { BrowserRouter, Switch, Route } from "react-router-dom";

import Login from "./pages/Login";
import Signup from "./pages/Signup";
import Profile from "./pages/Profile";

import LoggedOutRoute from "./components/LoggedOutRoute";
import Projects from "./pages/Projects";
import ResourceDetails from "./pages/ResourceDetails";
import Actions from "./pages/Actions";
import Home from "./pages/Home";
import ResourceVisualization from "./pages/ResourceVisualization";
import Product from "./pages/Product";
import Pricing from "./pages/Pricing";
import AdminActions from "./pages/AdminActions";


// const Home = () => <h1>Home</h1>;
// const Signup = () => <h1>Singup</h1>;
// const Login = () => <h1>Login</h1>;
// const Profile = () => <h1>Profile</h1>;
// const ResourceDetail = () => <h1>Detalle de Proyecto</h1>;

function Router() {
  return (
    <BrowserRouter>
      <Switch>
        <Route component={Home} path="/" exact />
        <LoggedOutRoute component={Signup} path="/signup" />
        <LoggedOutRoute component={Login} path="/login" />
        <Route component={Profile} path="/profile" />
        <Route component={Projects} path="/project" />
        <Route component={ResourceDetails} path="/resource/:resourceId" />
        <Route component={Actions} path="/actions" />
        <Route component={ResourceVisualization} path="/resourcevis" />
        <Route component={Pricing} path="/pricing" />
        <Route component={Product} path="/product" />
        <Route component={AdminActions} path="/admin" />
      </Switch>
    </BrowserRouter>
  );
}

export default Router;
