import { Form, Typography, Col, Row, Button, Input } from "antd";
import React from "react";
import NavbarCM from "../components/Navbar/NavbarCM";
import { useAuthInfo } from "../hook/authContext";
import "./styles/Actions.css";

export default function Login() {
  const [form] = Form.useForm();
  const { login } = useAuthInfo();
  function handleSubmit(userInfo) {
    login(userInfo);
  }
  return (
    <>
      <NavbarCM />
      <div className="p-text-div div-help"></div>
        <Row gutter={[16, 16]}>
          <Col xs={24} sm={24} md={{ offset: 8, span: 8 }}>
            <Typography.Title level={1}>Login</Typography.Title>
            <Form form={form} onFinish={handleSubmit} layout="vertical">
              <Form.Item name="email" label="Email:">
                <Input type="email" />
              </Form.Item>
              <Form.Item name="password" label="Password:">
                <Input.Password />
              </Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                type="primary"
                block
                size="large"
                size={"large"}
                shape="round"
                ghost="true"
                style={{
                  margin: "5px 0px",
                  borderColor: "green",
                  borderWidth: "2px",
                  color: "black",
                }}
              >
                Login
              </Button>
              <span className="form-input-login adjust-text">
                No account? Create one! <a href="/signup">here</a>
              </span>
            </Form>
          </Col>
        </Row>
      
    </>
  );
}
