import { Form, Typography, Col, Row, Button, Input, message } from "antd";
import React from "react";
import NavbarCM from "../components/Navbar/NavbarCM";
import { signupFn } from "../services/auth";
import "./styles/Actions.css";

export default function Signup() {
  const [form] = Form.useForm();

  async function handleSubmit(userInfo) {
    try {
      await signupFn(userInfo);
      message.success("Account Created");
    } catch (error) {
      message.error(error.response.data.message);
    }
  }
  return (
    <div>
      <NavbarCM />
      <div className="p-text-div div-help"></div>
      <Row gutter={[16, 16]}>
        <Col xs={24} sm={24} md={{ offset: 8, span: 8 }}>
          <Typography.Title level={1}>Sign up</Typography.Title>
          <Form form={form} onFinish={handleSubmit} layout="vertical">
            <Form.Item name="username" label="Username:">
              <Input type="text" />
            </Form.Item>
            <Form.Item name="email" label="Email:">
              <Input type="email" />
            </Form.Item>
            <Form.Item name="password" label="Password:">
              <Input.Password />
            </Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              style={{
                margin: "5px 0px",
                borderColor: "green",
                borderWidth: "2px",
                color: "black",
              }}
              type="primary"
              block
              size="large"
              size={"large"}
              shape="round"
              ghost="true"
            >
              Sign up
            </Button>
            <span className="form-input-login adjust-text">
              Already have an account? Login <a href="/login">here</a>
            </span>
          </Form>
        </Col>
      </Row>
    </div>
  );
}
