import React, { useState, useEffect } from "react";
import {
  Button,
  Form,
  Input,
  message,
  Typography,
  Col,
  Row,
  Avatar,
  Upload,
  Switch,
  notification,
  Select,
} from "antd";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import axios from "axios";
import { createResourcePy } from "../services/projects";

const { Text } = Typography;
const { Option } = Select;

function ProjectCreateForm({ closeModal, closeModalDelete }) {
  const [loadingLogo, setLoadingLogo] = useState(false);
  const [handleProjectInfo, setHandleProjectInfo] = useState({});
  const [img, setImg] = useState(null);

  const uploadButton = (
    <div>
      {loadingLogo ? <LoadingOutlined /> : <PlusOutlined />}
      <div style={{ marginTop: 8 }}>Upload Image</div>
    </div>
  );

  //=================================================
  const handleCoso = async (file) => {
    const fdata = new FormData();
    fdata.append("file", file);
    console.log("seste es el file", file);

    const cloudiaryApi =
      "https://api.cloudinary.com/v1_1/cristhianov/image/upload";
    fdata.append("upload_preset", "PPTracker");
    setLoadingLogo(true);
    const { data } = await axios.post(cloudiaryApi, fdata);
    console.log(data.secure_url);
    setHandleProjectInfo({ ...handleProjectInfo, url: data.secure_url });
    setLoadingLogo(false);
  };

  const handleChangeText = (e) => {
    setHandleProjectInfo({
      ...handleProjectInfo,
      [e.target.name]: e.target.value,
    });
    console.log(handleProjectInfo);
  };

  const sendData = async () => {
    const { data: project } = await createResourcePy(handleProjectInfo);
    message.success("Proyecto creado exitosamente");
    closeModal();
  };

  return (
    <>
      <Text>Project Code:</Text>
      <Input
        name="projectCode"
        label="Project's Code"
        onChange={handleChangeText}
        type="number"
        rules={[{ required: true, message: "Please input the project Code" }]}
      />

      <Text>Project Name:</Text>
      <Input
        name="projectName"
        onChange={handleChangeText}
        label="Project Name"
        rules={[{ required: true, message: "Please input the Project Name" }]}
      />
      <Text>Project Scope:</Text>
      <Input
        name="projectScope"
        onChange={handleChangeText}
        label="Define Project`s Scope"
        rules={[
          {
            required: true,
            message: "Please input the Project's Scope",
          },
        ]}
      />
      <Text>Project Phase:</Text>
      <Input
        name="projectPhase"
        onChange={handleChangeText}
        label="Define Project`s Phase"
        rules={[
          {
            required: true,
            message: "Please input the Project's Phase",
          },
        ]}
      />
      <Text>Strategy Objective:</Text>
      <Input
        name="strategyObjective"
        onChange={handleChangeText}
        label="Strategy Objective"
        rules={[
          { required: true, message: "Please input the Strategy Objective" },
        ]}
      />
      <Text>Responsible Area:</Text>
      <Input
        name="responsibleArea"
        onChange={handleChangeText}
        label="Responsible Area"
        rules={[
          { required: true, message: "Please input the Responsible Area" },
        ]}
      />
      <Text>Project Leader:</Text>
      <Input
        name="projectLeader"
        onChange={handleChangeText}
        label="Project's Leader Name"
        rules={[
          {
            required: true,
            message: "Please input the Project's Leader Name",
          },
        ]}
      />
      <Text>Project Team:</Text>
      <Input
        name="projectTeam"
        onChange={handleChangeText}
        label="Project's Team members"
        rules={[
          {
            required: true,
            message: "Please input the Project's Team members",
          },
        ]}
      />

      <Text>Month Updates:</Text>
      <Input
        name="monthUpdate"
        onChange={handleChangeText}
        label="Project's Updates - Optional -"
        rules={[
          {
            required: true,
            message: "Please input the Project's Month Updates",
          },
        ]}
      />
      <Text>Project Image:</Text>
      <Input
        onChange={handleChangeText}
        name="url"
        label="Project`s image"
        rules={[
          {
            message: "Please input the Project's Image",
          },
        ]}
      />
      <Upload
        name="url"
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        beforeUpload={handleCoso}
      >
        {img ? (
          <img src={img} alt="avatar" style={{ width: "100%" }} />
        ) : (
          uploadButton
        )}
      </Upload>

      <Text>Total Budget:</Text>
      <Input
        name="totalBudget"
        onChange={handleChangeText}
        type="number"
        label="Total Budget"
        rules={[
          {
            required: true,
            message: "Please input the Project's Total Budget",
          },
        ]}
      />

      <Text>Anual Budget:</Text>

      <Input
        name="anualBudget"
        onChange={handleChangeText}
        type="number"
        label="Project Anual Budget"
        rules={[
          {
            required: true,
            message: "Please input the Project's Anual Budget",
          },
        ]}
      />

      <Text>CPI:</Text>
      <Input
        name="CPI"
        onChange={handleChangeText}
        type="number"
        label="Project Bugdet CPI Indicador"
        rules={[
          {
            required: true,
            message: "Please input the Project's CPI",
          },
        ]}
      />
      <Text>SPI:</Text>
      <Input
        name="SPI"
        onChange={handleChangeText}
        type="number"
        label="Project Schedule Indicator SPI"
        rules={[
          {
            required: true,
            message: "Please input the Project's SPI",
          },
        ]}
      />
      <Text>Project Month Updates:</Text>
      <Input
        name="projectMonthUpdates"
        onChange={handleChangeText}
        label="Project`s Month Proggresses"
        rules={[
          {
            required: true,
            message: "Please input the Project's Month Proggresses",
          },
        ]}
      />
      <Text>Project Milestones:</Text>
      <Input
        name="projectMilestones"
        onChange={handleChangeText}
        label="Project`s Key Milestones"
        rules={[
          {
            required: true,
            message: "Please input the Project's Key Milestones",
          },
        ]}
      />
      <Text>Project Viewers:</Text>
      <Input
        name="projectViewers"
        onChange={handleChangeText}
        label="Define Project`s Viewers"
        rules={[
          {
            required: true,
            message: "Please input the Project's Viewers",
          },
        ]}
      />
      <Text> Project Changers:</Text>
      <Input
        name="projectViewChangers"
        onChange={handleChangeText}
        label="Define Project`s Changers"
        rules={[
          {
            required: true,
            message: "Please input the Project's Changers",
          },
        ]}
      />

      <Text>Project Plan Schedule:</Text>
      <Input
        name="projectPlanSchedule"
        onChange={handleChangeText}
        type="number"
        label="Define Project`s Plan  Schedule"
        rules={[
          {
            required: true,
            message: "Please input the Project's Plan Schedule",
          },
        ]}
      />
      <Text>Project Real Schedule:</Text>
      <Input
        name="projectRealSchedule"
        onChange={handleChangeText}
        type="number"
        label="Define Project`s Real  Schedule"
        rules={[
          {
            required: true,
            message: "Please input the Project's Real Schedule",
          },
        ]}
      />
      <Text>Month Update:</Text>
      <Input
        name="monthUpdate"
        onChange={handleChangeText}
        label="Project's Updates - Optional -"
        rules={[
          {
            required: true,
            message: "Please input the Project's Team members",
          },
        ]}
      />

      <Button
        type="primary"
        block
        onClick={sendData}
        style={{
          margin: "5px 0px",
          borderColor: "green",
          borderWidth: "2px",
          color: "black",
        }}
        shape="round"
        ghost="true"
      >
        Confirm Project
      </Button>
    </>
  );
}

export default ProjectCreateForm;
