import { createContext, useEffect, useContext } from "react";
import useLocalStorage from "./useLocalStorage";
import { getCurrentUser, loginFn, logoutFn } from "../services/auth";
import { notification } from "antd";

const AuthContext = createContext();

export const AuthProvider = (props) => {
  //Un estado con el usuario en sesion (idealmente refeljado en LocalStorage)
  const [user, setUser] = useLocalStorage(null, "user");

  //Metodos para interactura con el usuario (login, logout, revisar si hay sesion en el server)

  async function login(user) {
    try {
      const { data } = await loginFn(user);
      setUser(data);
      notification["success"]({
        message: `Hello ${data.username}!`,
        description: "Thank you for logging in",
        duration: 5,
        style: {
          borderRadius: "20px",
        },
      });
    } catch (error) {
      notification["error"]({
        message: "Something went wrong",
        description: error.response.data.message,
        duration: 5,
        style: {
          borderRadius: "20px",
        },
      });
    }
  }

  async function logout(push) {
    push("/");
    await logoutFn();
    setUser(null);
  }
  useEffect(() => {
    //Preguntara al backend si hay un usuario en sesion,
    async function getSession() {
      const { data } = await getCurrentUser();
      if (data) {
        setUser(data);
      }
    }
    getSession();
  }, [setUser]);
  return (
    <AuthContext.Provider value={{ user, login, logout }}>
      {props.children}
    </AuthContext.Provider>
  );
};

export const useAuthInfo = () => {
  const authCtxValue = useContext(AuthContext);
  return authCtxValue;
};
