import axios from "axios";

//Variable de entorno

const baseURL =
  process.env.NODE_ENV === "production"
    ? "https://ppmanagers.herokuapp.com/api/auth"
    : "http:localhost:3001/api/auth";

// const isProduction = process.env.NODE_ENV === "production";
// const URL = isProduction ? "https://ppmanager.herokuapp.com/auth" : "http://localhost:3001/auth";
// axios.defaults.withCredentials = true;

export const _axios = axios.create({
  // baseURL: URL,
  baseURL,
  withCredentials: true,
  //si usamos req.user o referente a la session se debe usar withCredentials
  timeout: 10000,
});

export const signupFn = (user) => _axios.post("/signup", user);
export const loginFn = (user) => _axios.post("/login", user);
export const logoutFn = (_) => _axios.get("/logout");
export const getCurrentUser = (_) => _axios.get("/session");
