import axios from "axios";

//Variable de entorno

// const baseURL = "https://ppmanager.herokuapp.com/";
const baseURL =
  process.env.NODE_ENV === "production"
    ? "https://ppmanagers.herokuapp.com/api/project"
    : "http://localhost:3001/api/project";

// const isProduction = process.env.NODE_ENV === "production";
// const URL = isProduction ? "/project" : "http://localhost:3001/";
// axios.defaults.withCredentials = true;

export const _axios = axios.create({
  baseURL,
  //si usamos req.user o referente a la session se debe usar withCredentials
  timeout: 10000,
  withCredentials: true,
});
console.log(baseURL);

//Se crea conexion con endpoints

export const getAllResourcesPy = (project) => _axios.get("/project", project);
export const getOneResourcePy = (resourcePyId) =>
  _axios.get(`/project/${resourcePyId}`);
export const createResourcePy = (resourcePy) =>
  _axios.post("/project/create", resourcePy);
export const updateResourcePy = (resourcePyId, updates) =>
  _axios.patch(`/project/${resourcePyId}`, updates);
export const deleteResourcePy = (resourcePyId) =>
  _axios.delete(`/project/${resourcePyId}`);

export const updateProjectImg = (projectImg) =>
  _axios.post("/projectimg/change", { projectImg });
