import React, { useState, useEffect } from "react";
import {
  Button,
  Form,
  Input,
  message,
  Typography,
  Col,
  Row,
  Avatar,
  Upload,
  Switch,
  notification,
  Select,
  Skeleton,
  select,
} from "antd";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import axios from "axios";
import { getAllResourcesPy, deleteResourcePy } from "../services/projects";

const { Option, OptGroup } = Select;

function ProjectDeleteForm({ closeModalDelete, closeModal }) {
  const [currentProjects, setCurrentProjects] = useState(null);
  const [projectToDelete, setProjectToDelete] = useState(null);
  useEffect(() => {
    async function getResources() {
      const {
        data: { projects },
      } = await getAllResourcesPy();
      setCurrentProjects(projects);
    }
    getResources();
  }, []);

  //   const handleChangeText = (e) => {
  //     setProjectToDelete({
  //       ...projectToDelete,
  //       [e.target.value]:e.target.name
  //     //   [e.target.name]: e.target.value,
  //     });
  //     console.log(projectToDelete);
  //   };

  function handleChangeText(value) {
    console.log(`selected ${value}`);
    setProjectToDelete(value);
  }

  const sendData = async () => {
    const { data: project } = await deleteResourcePy(projectToDelete);
    message.success("Proyecto Deleted Sucessfully");
    closeModal();
  };
  return (
    <div>
      <Select
        style={{ width: 200 }}
        placeholder="Select the project you want to delete"
        onChange={handleChangeText}
        name="projectSelected"
      >
        <OptGroup>
          {currentProjects ? (
            currentProjects.map((c, i) => (
              <Option key={i} value={c._id}>
                {c.projectName}
              </Option>
            ))
          ) : (
            <Skeleton active />
          )}
        </OptGroup>
      </Select>

      <Button
        style={{
          margin: "5px 0px",
          borderColor: "green",
          borderWidth: "2px",
          color: "black",
        }}
        type="primary"
        block
        size="large"
        size={"large"}
        shape="round"
        ghost="true"
        onClick={sendData}
      >
        Delete Project
      </Button>
    </div>
  );
}

export default ProjectDeleteForm;
